:root {
  --main-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif, Arial, Helvetica;
  font-size: 12px;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.logo_png {
  width: 40px;
  height: 40px;
  margin-top: 10px;
  display: block;
}

.nav__list {
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  justify-content: center;
  height: 15px;
  margin-right: 20px;
  font-size: 12px;
  font-weight: 500;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  justify-content: center;
  align-items: center;
  height: 15px;
  padding: 10px 0;
  font-size: 12px;
  text-decoration: none;
  display: block;
}

.is-active, .nav__list :hover {
  color: var(--main-color);
}

a.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
}
